"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { SectorDetail } from "@/components/sector-detail"
import { TrendingUp, TrendingDown, Minus } from "lucide-react"

type SectorStatus = "normal" | "warning" | "critical"

interface Sector {
  id: string
  name: string
  status: SectorStatus
  efficiency: number
  pressure: number
  alerts: string[]
  trend: "up" | "down" | "stable"
}

export function SectorGrid() {
  const [selectedSector, setSelectedSector] = useState<Sector | null>(null)

  // Mock data - in production this would come from real-time API
  const sectors: Sector[] = [
    {
      id: "233",
      name: "Sector 233",
      status: "critical",
      efficiency: 68,
      pressure: 42,
      alerts: ["Posible fuga", "Sobrepresión +20%"],
      trend: "down",
    },
    {
      id: "234",
      name: "Sector 234",
      status: "warning",
      efficiency: 82,
      pressure: 38,
      alerts: ["Consumo atípico"],
      trend: "up",
    },
    {
      id: "145",
      name: "Sector 145",
      status: "warning",
      efficiency: 79,
      pressure: 35,
      alerts: ["Baja disponibilidad prevista"],
      trend: "down",
    },
    {
      id: "089",
      name: "Sector 089",
      status: "normal",
      efficiency: 91,
      pressure: 40,
      alerts: [],
      trend: "stable",
    },
    {
      id: "156",
      name: "Sector 156",
      status: "normal",
      efficiency: 88,
      pressure: 39,
      alerts: [],
      trend: "stable",
    },
    {
      id: "201",
      name: "Sector 201",
      status: "critical",
      efficiency: 62,
      pressure: 45,
      alerts: ["Fuga confirmada", "Pérdida energética alta"],
      trend: "down",
    },
    {
      id: "312",
      name: "Sector 312",
      status: "normal",
      efficiency: 89,
      pressure: 38,
      alerts: [],
      trend: "stable",
    },
    {
      id: "078",
      name: "Sector 078",
      status: "warning",
      efficiency: 75,
      pressure: 36,
      alerts: ["Consumo elevado por calor"],
      trend: "up",
    },
  ]

  const getStatusColor = (status: SectorStatus) => {
    switch (status) {
      case "normal":
        return "bg-success/10 border-success text-success"
      case "warning":
        return "bg-warning/10 border-warning text-warning"
      case "critical":
        return "bg-destructive/10 border-destructive text-destructive"
    }
  }

  const getStatusLabel = (status: SectorStatus) => {
    switch (status) {
      case "normal":
        return "Normal"
      case "warning":
        return "Alerta"
      case "critical":
        return "Crítico"
    }
  }

  const getTrendIcon = (trend: "up" | "down" | "stable") => {
    switch (trend) {
      case "up":
        return <TrendingUp className="h-4 w-4" />
      case "down":
        return <TrendingDown className="h-4 w-4" />
      case "stable":
        return <Minus className="h-4 w-4" />
    }
  }

  return (
    <>
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="font-heading text-xl font-semibold tracking-tight">Mapa de Sectores</h2>
          <div className="flex gap-2">
            <Badge variant="outline" className="bg-success/10 border-success text-success text-xs font-medium">
              Normal
            </Badge>
            <Badge variant="outline" className="bg-warning/10 border-warning text-warning text-xs font-medium">
              Alerta
            </Badge>
            <Badge
              variant="outline"
              className="bg-destructive/10 border-destructive text-destructive text-xs font-medium"
            >
              Crítico
            </Badge>
          </div>
        </div>

        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
          {sectors.map((sector) => (
            <Card
              key={sector.id}
              className={`p-4 cursor-pointer transition-all hover:shadow-md border-2 ${getStatusColor(sector.status)}`}
              onClick={() => setSelectedSector(sector)}
            >
              <div className="space-y-2">
                <div className="flex items-start justify-between">
                  <div>
                    <p className="font-heading font-semibold text-sm tracking-tight">{sector.name}</p>
                    <p className="text-xs opacity-80 font-medium">{getStatusLabel(sector.status)}</p>
                  </div>
                  {getTrendIcon(sector.trend)}
                </div>

                <div className="space-y-1">
                  <div className="flex justify-between text-xs">
                    <span className="opacity-80">Eficiencia</span>
                    <span className="font-medium tabular-nums">{sector.efficiency}%</span>
                  </div>
                  <div className="flex justify-between text-xs">
                    <span className="opacity-80">Presión</span>
                    <span className="font-medium tabular-nums">{sector.pressure} PSI</span>
                  </div>
                </div>

                {sector.alerts.length > 0 && (
                  <div className="pt-2 border-t border-current/20">
                    <p className="text-xs font-medium tabular-nums">{sector.alerts.length} alerta(s)</p>
                  </div>
                )}
              </div>
            </Card>
          ))}
        </div>
      </div>

      {selectedSector && <SectorDetail sector={selectedSector} onClose={() => setSelectedSector(null)} />}
    </>
  )
}
